import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  Paper,
  Button,
  Alert,
  Snackbar,
  Grid,
  Card,
  CardContent,
  LinearProgress,
  List,
  ListItem,
  ListItemText,
  Divider,
} from '@mui/material';

interface MergeStatus {
  local: number;
  cloud: number;
  onlyLocal: number;
  onlyCloud: number;
  conflicts: number;
}

const DataMerger: React.FC = () => {
  const [localData, setLocalData] = useState<any>({ packages: [], finance: [], users: [] });
  const [cloudData, setCloudData] = useState<any>({ packages: [], finance: [], users: [] });
  const [mergeStatus, setMergeStatus] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [alert, setAlert] = useState<{ open: boolean; message: string; severity: 'success' | 'error' }>({
    open: false,
    message: '',
    severity: 'success',
  });

  useEffect(() => {
    loadAllData();
  }, []);

  const loadAllData = async () => {
    setLoading(true);
    try {
      // localStorage
      const localPackages = JSON.parse(localStorage.getItem('packages') || '[]');
      const localFinance = JSON.parse(localStorage.getItem('finance') || '[]');
      const localUsers = JSON.parse(localStorage.getItem('users') || '[]');

      setLocalData({ packages: localPackages, finance: localFinance, users: localUsers });

      // Supabase
      const mockCloudData = {
        packages: [],
        finance: [],
        users: [],
      };

      setCloudData(mockCloudData);
    } catch (error) {
      console.error(':', error);
      setAlert({ open: true, message: '', severity: 'error' });
    } finally {
      setLoading(false);
    }
  };

  const analyzeData = () => {
    if (!localData || !cloudData) return;

    const status: any = {
      packages: { local: 0, cloud: 0, onlyLocal: 0, onlyCloud: 0, conflicts: 0 },
      finance: { local: 0, cloud: 0, onlyLocal: 0, onlyCloud: 0 },
      users: { local: 0, cloud: 0, onlyLocal: 0, onlyCloud: 0 },
    };

    // 
    status.packages.local = localData.packages.length;
    status.packages.cloud = cloudData.packages.length;
    status.packages.onlyLocal = localData.packages.length;
    status.packages.onlyCloud = cloudData.packages.length;
    status.packages.conflicts = 0;

    // 
    status.finance.local = localData.finance.length;
    status.finance.cloud = cloudData.finance.length;
    status.finance.onlyLocal = localData.finance.length;
    status.finance.onlyCloud = cloudData.finance.length;

    // 
    status.users.local = localData.users.length;
    status.users.cloud = cloudData.users.length;
    status.users.onlyLocal = localData.users.length;
    status.users.onlyCloud = cloudData.users.length;

    setMergeStatus(status);
  };

  const mergeData = async () => {
    setLoading(true);
    try {
      // 
      const mergedPackages = [...localData.packages];
      const mergedFinance = [...localData.finance];
      const mergedUsers = [...localData.users];

      // localStorageSupabase
      localStorage.setItem('packages', JSON.stringify(mergedPackages));
      localStorage.setItem('finance', JSON.stringify(mergedFinance));
      localStorage.setItem('users', JSON.stringify(mergedUsers));

      setAlert({ open: true, message: '', severity: 'success' });
      analyzeData(); // 
    } catch (error) {
      console.error(':', error);
      setAlert({ open: true, message: '', severity: 'error' });
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        
      </Typography>
      <Typography variant="body1" color="text.secondary" sx={{ mb: 4 }}>
        
      </Typography>

      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                 (localStorage)
              </Typography>
              <Typography variant="body2" color="text.secondary">
                 : {localData.packages.length} <br />
                 : {localData.finance.length} <br />
                 : {localData.users.length} 
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                 (Supabase)
              </Typography>
              <Typography variant="body2" color="text.secondary">
                 : {cloudData.packages.length} <br />
                 : {cloudData.finance.length} <br />
                 : {cloudData.users.length} 
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
              <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                <Button variant="outlined" onClick={analyzeData}>
                  
                </Button>
                <Button variant="contained" onClick={mergeData} disabled={loading}>
                  {loading ? '...' : ''}
                </Button>
                <Button variant="outlined" onClick={loadAllData}>
                  
                </Button>
              </Box>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {loading && (
        <Box sx={{ mt: 3 }}>
          <LinearProgress />
        </Box>
      )}

      {mergeStatus && (
        <Paper sx={{ p: 3, mt: 3 }}>
          <Typography variant="h6" gutterBottom>
            
          </Typography>

          <Grid container spacing={3}>
            <Grid item xs={12} md={4}>
              <Typography variant="subtitle1" gutterBottom>
                
              </Typography>
              <List dense>
                <ListItem>
                  <ListItemText primary={`: ${mergeStatus.packages.local} `} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`: ${mergeStatus.packages.cloud} `} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`: ${mergeStatus.packages.onlyLocal} `} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`: ${mergeStatus.packages.conflicts} `} />
                </ListItem>
              </List>
            </Grid>

            <Grid item xs={12} md={4}>
              <Typography variant="subtitle1" gutterBottom>
                
              </Typography>
              <List dense>
                <ListItem>
                  <ListItemText primary={`: ${mergeStatus.finance.local} `} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`: ${mergeStatus.finance.cloud} `} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`: ${mergeStatus.finance.onlyLocal} `} />
                </ListItem>
              </List>
            </Grid>

            <Grid item xs={12} md={4}>
              <Typography variant="subtitle1" gutterBottom>
                
              </Typography>
              <List dense>
                <ListItem>
                  <ListItemText primary={`: ${mergeStatus.users.local} `} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`: ${mergeStatus.users.cloud} `} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`: ${mergeStatus.users.onlyLocal} `} />
                </ListItem>
              </List>
            </Grid>
          </Grid>
        </Paper>
      )}

      <Alert severity="info" sx={{ mt: 3 }}>
        <Typography variant="body2">
          <strong></strong>
        </Typography>
      </Alert>

      <Snackbar
        open={alert.open}
        autoHideDuration={4000}
        onClose={() => setAlert({ ...alert, open: false })}
      >
        <Alert severity={alert.severity} onClose={() => setAlert({ ...alert, open: false })}>
          {alert.message}
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default DataMerger;
